@import url("https://fonts.googleapis.com/css2?family=Hammersmith+One&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap');


* {
  margin: 0;
  padding: 0;
}

body {
  background-color: rgb(255, 255, 255);
}

.navbar {
  height: auto;
  background-color: rgb(252, 249, 249);
}
h2{
  font-size: 35px;
  font-family: 'Fredoka One', cursive;
}

p{
  font-size: 20px;
}

a {
  padding: 10px;  
  color: black;
  text-decoration: none;
}
a:hover {
  color: #e74c3c;
 }
.list {
  display: none;
}
.right-nav {
  display: none;
}
.text-container{
  position:relative;
  top:25px;
  text-align:center;
  background-color:rgb(252, 249, 249);
}

.searchbar {
  margin-bottom: auto;
  margin-top: auto;
  height: 60px;
  background-color: #ffffff;
  border-radius: 30px;
  padding: 10px;
  position: relative;
  left: 20px;
}

.search_input {
  color: black;
  border: 0;
  outline: 0;
  background: none;
  width: 0;
  caret-color: transparent;
  line-height: 40px;
  transition: width 0.4s linear;
}

.searchbar:hover > .search_input {
  padding: 0 10px;
  width: 450px;
  caret-color: red;
  transition: width 0.4s linear;
}

.searchbar:hover > .search_icon {
  background: white;
  color: #e74c3c;
}

.search_icon {
  height: 40px;
  width: 40px;
  float: right;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  color: white;
  text-decoration: none;
}
.text-a {
  text-decoration: none;
  color: black;
  font-size: 25px;
  font-family: "Hammersmith One", sans-serif;
}
.category-cont{
  margin-top:0;
  width: 100%;
  height: auto;
  background-color:  rgb(252, 249, 249);
  text-align: center;
  border-radius: 15px;
  position: relative;
  z-index: 1;
  }

.carousel-img1{
  text-align: center;
  position: relative;
  }
 
  .carousel-img2{
    position: relative;
    text-align: center;
    z-index: -1;
    margin: auto;
  }
  

.Category-a{
  text-decoration: none;
}
.carousel-div{
  position: relative;
  text-align: center;
  margin:auto
}
.main-pic{
  border-radius: 15px;
}
.footer{
  position: relative;
  margin-top:290px;
  bottom:0;
  height: 300px;
  background-color: black;
  color:white ;
}
.right-nav1{
  display: none;
}

@media only screen and (max-width: 1100px) {
  .searchbar:hover > .search_input {
    padding: 0 10px;
    width: 200px;
    caret-color: red;
    transition: width 0.4s linear;
  }

  .right-nav {
    width: 250px;
    position: fixed;
    right: 0;
    background-color: rgb(252, 249, 249);
    height: 100%;
    display: inline-block;
    z-index: 1;
  }

  .text-a {
    text-decoration: none;
    color: black;
    font-size: 15px;
    font-family: "Hammersmith One", sans-serif;
  }

  .list {
    display: inline-block;
    margin: 10px;
  }

  .list:hover {
    color: #e74c3c;
    cursor: pointer;
  }

  .nav-text {
    display: none;
  }

  .right-a {
    color: black;
    display: block;
    float: none;
    overflow: hidden;
    padding-top: 5px;
    text-decoration: none;
    font-size: 20px;
    font-family: "Hammersmith One", sans-serif;
  }

  .right-nav-text {
    font-size: 15px;
    margin-top: 50px;
    font-family: "Hammersmith One", sans-serif;
  }

  .Atletic {
    font-size: 20px;
    margin-top: 25px;
  }

  .right-buttons {
    color: black;
    display: block;
    padding-top:25px ;
    font-size: 20px;
    font-family: "Hammersmith One", sans-serif;
    left: -20px;
    position: relative;
  }
  .carousel-img1{
    display: none;
  }
  .carousel-img2{
    display: none;
  }

  .carousel-img1{
    text-align: center;
    position: relative;
    }
   
    .right-nav1{
      background-color: red;
    }
    
}
